body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Button {
  border: none;
  color: #c1a27a;
  background-color: transparent;
  font-size: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  transition: color 0.3s;
}

.Button:hover {
  cursor: pointer;
  color: #fff;
}


@media screen and (max-width: 610px) {
  .Button {
    display: none;
  }
}
.header {
  background-color: #333;
  color: #c1a27a;
}

.heading {
  margin: 0;
  padding-top: 20px;
  font-size: 2.5em;
}

.searchForm {
  display: grid;
  grid-template-columns: 4fr 1fr;
  padding: 0 25vw;
}

.input {
  border: none;
  border-bottom: 4px solid #c1a27a;
  margin: 0 10px 20px;
  padding: 20px 10px 0;
  text-align: center;
  font-size: 2.5em;
  outline: none;
  background-color: transparent;
  color: #c1a27a;
  caret-color: #c1a27a;
  transition: color 0.3s;
}

.input:focus {
  color: #fff;
}

@media screen and (max-width: 925px) {
  .searchForm {
    padding: 0 10vw
  }
}
.chart {
  position: relative;
}

.min {
  position: absolute;
  top: -8px;
  left: -15px;
  font-size: 0.8em;
}

.max {
  position: absolute;
  bottom: 35px;
  left: -15px;
  font-size: 0.8em;
}
table {
  width: 70vw;
  margin: 20px 15vw;
  border-left: 2px solid #c1a27a;
  border-right: 2px solid #c1a27a;
}

tr {
  border-bottom: 2px solid rgb(193, 162, 122, 0.3);
}

thead tr {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  color: #c1a27a;
  font-size: 1.2em;
  text-shadow: 0 1px rgba(0,0,0,0.3);
  padding-bottom: 15px;
}

tbody {
  display: block;
  height: 70vh;
  overflow-y: scroll;
}

tbody tr {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: center;
}

td {
  padding: 10px;
}

td:first-of-type, td:first-of-type > div {
  height: 150px;
}

tbody svg {
  height: 100px;
  width: 100%;
}

@media screen and (max-width: 925px) {
  table {
    width: 90vw;
    margin: 20px 5vw;
  }
}
